.header {
  background-color:  #1E0143;
  height: 98px;
  display: flex;
}
.logo {
  width: 200px;
  height: 50px;
  border-radius: 12px;
  background: #865BBF;
}
nav{
  display: flex;
  column-gap: 75px;
 
}
.nav-link {
  color: var(--btn-txt, #FFF);
  font-family: Montserrat Alternates;
  font-size: 20px;
  font-weight: 400;
  line-height: normal;
  transition: transform 250ms cubic-bezier(0.4, 0, 0.2, 1);
  &:hover {
    transform: scale(110%);
  }

}
.header-container {
  display: flex;
  align-items: center;
  justify-content: space-between;
}
.container {
  margin: 0 auto;
  width: 1360px;
}